.footer {
  z-index: -1;

  &__title {
    margin: 0;
    margin-bottom: 48px;

    font-weight: 700;
    font-size: 32px;
    line-height: 100%;
    text-align: left;

    @include on-tablet {
      margin-bottom: 56px;
      font-size: 40px;
    }

    @include on-desktop {
      font-size: 48px;
    }
  }
}
